import React from "react";

const number = "1";

const jsxElement1 = <p className="greeting">inserted JSX {number}</p>;

const jsxElement2 = React.createElement(
  "p",
  { className: "greeting" },
  "inserted JSX 2"
);

class JSXChild extends React.Component {
  render() {
    return (
      <div>
        <p>JSX:</p>
        {jsxElement1}
        {jsxElement2}
      </div>
    );
  }
}

export { JSXChild };

class NoJSX extends React.Component {
  render() {
    return (
      <div>
        <p>No JSX:</p>
        <p className="greeting">non-inserted JSX 1</p>
        <p className="greeting">non-inserted JSX 2</p>
      </div>
    );
  }
}

export { NoJSX };

class JSX extends React.Component {
  render() {
    return (
      <div>
        <h1>JSX:</h1>
        <JSXChild />
        <NoJSX />
      </div>
    );
  }
}

export default JSX;
